.window{
  height:100vh;
  width:100vw;
  background-color:#42033d;
  display:flex;
  justify-content:center;
  align-items:center;
}

.main{
  width:80%;
  background:white;
  height:85%;
  display:flex;
}

.sidebar{
  flex-grow:3;
  width:25%;
  background:#084c61;
  color:#BCE7FD;
  display:flex;
  align-items:center;
  flex-direction:column;
}

.insights{
  flex-grow:9;
  width:75%;
  overflow-y:auto;
}

@media(max-width: 1272px){
  .main{
    width: 100%;
  }
}

@media(max-width: 1112px){
  .main{
    display: block;
  }
  .sidebar{
    width:100%;
  }
  .insights{
    width:100%;
  }
}

.none{
  display: none;
}

.shrinkedMain{
  background:white;
  height:80%;
  display:flex;
  width:20%;
}

p{
  margin:1px;
  font-size: 14px;
}